	import {queryCard} from '@/common/api.js'
	import cardConfig from '@/common/card-config.js'
	
    export default {
        onLoad() {
            this.color = this.BASECOLOR;
			queryCard(2).then(res=>{
				
				this.used=res.cardOwnList.filter(item=>item.status===cardConfig.status.used);
				this.disabled=res.cardOwnList.filter(item=>item.status===cardConfig.status.disabled);
				
			}).catch((res)=>{
				
			})
			this.ossDownloadUrl = this.HTTPCONFIG.ossDownloadUrl;
			
        },

        data() {
            return {
				ossDownloadUrl:'',
				
                color: '',
                TabCur: 0,
                scrollLeft: 0,
                tapTopContent: ['我的卡', '失效的卡'],
                cardList:[
                	
                ],
				used:[],
				disabled:[],

            }
        },
        methods: {
            tabSelect(e) {
                this.TabCur = e.currentTarget.dataset.id;
                this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
            },
            tapBodyScroll(e) {
                this.TabCur = e.detail.current;
                this.scrollLeft = (e.detail.current - 1) * 60
            },
			toMycard(item){
				this.COMMONFUNCTION.toPage('/pages/mycards/card?grpId='+item.grpCode+'&cardId='+item.cardType)
			},
        }
    }
